html {
  --main-blue-color: #00acdc;
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.header-text {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.is-active, .header-text:hover {
  color: var(--main-blue-color);
}

.header-text:after {
  content: "";
  background-color: var(--main-blue-color);
  border-radius: 2px;
  width: 0%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.is-active:after {
  width: 100%;
}

